@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.weather-container{font-family:"Lato", sans-serif;display:inline-block;position:relative;margin:5px;width:calc(30% - 1px);height:400px;border:1px solid #e6e6e6;border-radius:3%;text-decoration:none;margin-top:0%;-webkit-animation:appear 500ms ease-out forwards;animation:appear 500ms ease-out forwards}.weather-container .weather-icon-2{position:relative;width:90px;height:90px;top:0;float:right;margin:0px 50px 0 0;-webkit-animation:weather-icon-move 5s ease-in-out infinite;animation:weather-icon-move 5s ease-in-out infinite}.weather-container ul{list-style-type:none}.weather-container .text{color:#b8b8b8;font-size:20px;font-weight:600}.weather-container .max-temp{text-align:center;font-weight:500;font-size:40px;color:#b8b8b8}.weather-container .min-temp{padding-top:10px;font-weight:400;font-size:16px;color:#b8b8b8}.weather-container h5{margin-left:40px}.weather-container h5{color:#b8b8b8;bottom:0;left:40%;opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px);-webkit-animation:title-appear 500ms ease-out 500ms forwards;animation:title-appear 500ms ease-out 500ms forwards}

body{background-color:#ebf2f6;background-image:url(/weather-app/static/media/pexels.b2be3087.jpg);background-size:cover;font-family:"Lato", sans-serif}*,*:before,*:after{box-sizing:border-box}.weather-wrapper{margin-top:20px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.weather-wrapper input{width:400px;height:50px}.weather-wrapper .btn{border:none;background-color:#3a527b;color:white;padding:16px 50px;display:inline-block;margin:15px 30px;text-transform:uppercase;letter-spacing:1px;font-weight:700;box-shadow:0 0 40px 40px #3498db inset, 0 0 0 0 #3498db;transition:all 150ms ease-in-out}.weather-wrapper .btn:hover{box-shadow:0 0 10px 0 #3498db inset, 0 0 10px 4px}.weather-card{margin:20px 5px;border-radius:20px;position:relative;overflow:hidden;width:670px;height:170px;background-color:#fff;box-shadow:0px 0px 25px 1px rgba(50,50,50,0.1);-webkit-animation:appear 500ms ease-out forwards;animation:appear 500ms ease-out forwards}.weather-card h1{position:absolute;font-weight:300;font-size:80px;color:#b8b8b8;bottom:0;left:40%;opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px);-webkit-animation:title-appear 500ms ease-out 500ms forwards;animation:title-appear 500ms ease-out 500ms forwards}.weather-card p{position:absolute;font-weight:300;font-size:28px;color:#d2d2d2;bottom:0;left:35px;-webkit-animation:title-appear 1s ease-out 500ms forwards;animation:title-appear 1s ease-out 500ms forwards}.weather-card p span{font-size:18px;font-weight:600}.weather-icon{position:relative;width:50px;height:50px;top:0;float:right;margin:40px 40px 0 0;-webkit-animation:weather-icon-move 5s ease-in-out infinite;animation:weather-icon-move 5s ease-in-out infinite}.sun{background:#ffcd41;border-radius:50%;box-shadow:rgba(255,255,0,0.1) 0 0 0 4px;-webkit-animation:light 800ms ease-in-out infinite alternate, weather-icon-move 5s ease-in-out infinite;animation:light 800ms ease-in-out infinite alternate, weather-icon-move 5s ease-in-out infinite}@-webkit-keyframes light{from{box-shadow:rgba(255,255,0,0.2) 0 0 0 10px}to{box-shadow:rgba(255,255,0,0.2) 0 0 0 17px}}@keyframes light{from{box-shadow:rgba(255,255,0,0.2) 0 0 0 10px}to{box-shadow:rgba(255,255,0,0.2) 0 0 0 17px}}.cloud{margin-right:60px;background:#b6cede;border-radius:20px;width:25px;height:25px;box-shadow:#b6cede 24px -6px 0 2px,#b6cede 10px 5px 0 5px,#b6cede 30px 5px 0 2px,#b6cede 11px -8px 0 -3px,#b6cede 25px 11px 0 -1px}.cloud:after{content:"";position:absolute;border-radius:10px;background-color:transparent;width:4px;height:12px;left:0;top:31px;-webkit-transform:rotate(30deg);transform:rotate(30deg);-webkit-animation:rain 800ms ease-in-out infinite alternate;animation:rain 800ms ease-in-out infinite alternate}@-webkit-keyframes rain{from{box-shadow:#2092a9 8px 0px,#2092a9 32px -6px,#2092a9 20px 0px}to{box-shadow:#2092a9 8px 6px,#2092a9 32px 0px,#2092a9 20px 6px}}@keyframes rain{from{box-shadow:#2092a9 8px 0px,#2092a9 32px -6px,#2092a9 20px 0px}to{box-shadow:#2092a9 8px 6px,#2092a9 32px 0px,#2092a9 20px 6px}}@-webkit-keyframes weather-icon-move{50%{-webkit-transform:translateY(-8px);transform:translateY(-8px)}}@keyframes weather-icon-move{50%{-webkit-transform:translateY(-8px);transform:translateY(-8px)}}.inspiration{margin-top:80px;color:#709fbe;font-family:"Lato", sans-serif;font-weight:300;font-size:24px;text-align:center}.inspiration a{color:#ff8f8f;font-weight:400;-webkit-animation:all 300ms ease-in-out;animation:all 300ms ease-in-out}@-webkit-keyframes appear{0%{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1.05);transform:scale(1.05)}75%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes appear{0%{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1.05);transform:scale(1.05)}75%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes title-appear{from{opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes title-appear{from{opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}

